@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap);
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Raleway', -apple-system, BlinkMacSystemFont, 'Segoe UI',
        'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 1.5em;
    text-align: center;
    background: url(/memorygame/static/media/background-shadow.14f6e0d6.jpeg);
    color: #fff;
}

button {
    font-family: 'Raleway', -apple-system, BlinkMacSystemFont, 'Segoe UI',
        'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
}

footer {
    font-size: 14px;
    display: flex;
    justify-content: space-around;
    position: relative;
    bottom: -40px;
}

a,
a:link,
a:visited {
    background-color: none;
    color: #fff;
    text-decoration: none;
}

.App {
    max-width: 860px;
    margin: 50px auto;
}

.main-button {
    background: none;
    border: 2px solid #fff;
    padding: 6px 12px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    font-size: 1em;
    border-radius: 6px;
    background-color: rgba(0, 0, 0, 0.5);
}
.main-button:hover {
    background: #c23866;
    color: #fff;
}

.card-grid {
    margin: 20px 40px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
}

.board-button {
    position: absolute;
    top: 10px;
    left: 10px;
}

.progress {
    height: -webkit-min-content;
    height: min-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 40px;
}

.count {
    display: grid;
    place-items: center;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid #fdfdfd;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    width: 212px;
    height: 60px;
}

.log-user {
    position: absolute;
    top: 10px;
    right: 10px;
}
.log {
    border: 1px solid white;
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 14px;
    font-weight: bold;
    width: 100px;
    height: 30px;
    border-radius: 10px;
    color: #fff;
}
.log:hover {
    background: #c23866;
}

@media only screen and (max-width: 600px) {
    .card-grid {
        margin: 20px 10px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 10px;
    }
    .progress {
        display: flex;
        margin: 20px;
    }
    .count {
        width: 110px;
        height: 60px;
        border-radius: 10px;
    }
    .log-user .board-button {
        border-radius: 5px;
        font-size: 14px;
        padding: 0 8px;
    }
}

.card {
    position: relative;
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

/* front of card - the picture */
.card .front {
    position: absolute;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition: all ease-in 0.2s;
}

.flipped .front {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/* back of card - the cover */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .back {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition-delay: 0s;
}

.level-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    width: 50%;
    margin: 50px auto;
}

.level-card {
    position: relative;
    display: grid;
    place-items: center;
}

.level-card img {
    width: 50%;
    border-radius: 6px;
    display: block;
}
.level-card .locked {
    top: 0;
    width: 40%;
    position: absolute;
}

.level-card h5 {
    margin: 0 !important;
}
#profile-card {
    border: 2px solid #fdfdfd;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 2fr;
    grid-template-areas:
        'avatar username'
        'avatar point';

    height: 60px;
    max-width: 212px;
    padding: 0 10px;
}

#profile-card img {
    grid-area: avatar;
    width: 100%;
    border-radius: 50%;

    width: 50px;
    height: 50px;
    margin-left: 5px;
}

#profile-card .username {
    display: flex;
    justify-content: space-between;
    grid-area: username;
    align-items: center;
    font-weight: bold;

    font-size: 18px;
    font-weight: bold;
    line-height: 25px;
}

#profile-card .level {
    background-color: rgba(255, 255, 255, 0.26);
    border-radius: 50%;
    border: 2px solid #c23866;

    margin-right: 5px;
    font-size: 12px;
    line-height: 14px;
    height: 20px;
    width: 20px;
}

#profile-card .point {
    position: relative;
    grid-area: point;
}

#profile-card .point label {
    position: absolute;
    display: block;
    margin: 0 auto;
    font-weight: bold;
    font-size: 12px;

    justify-self: start;
    font-weight: bold;
    font-size: 12px;
}

#profile-card .point label span {
    color: rgb(150, 255, 52);
}

#profile-card .point input {
    margin-left: -5px;
}

