.modal-backdrop {
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    color: #000;
}

.modal {
    position: relative;
    padding: 30px;
    max-width: 480px;
    margin: 100px auto;
    background: rgb(224, 224, 224);
    border-radius: 10px;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    border: none;
    background: none;
    font-size: 30px;
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    .modal {
        margin: 50px 20px;
        border-radius: 20px;
    }
}
#form-container {
    height: 100%;
    width: 100%;
}
#form-container h3 {
    font-weight: bold;
    color: #ff669e;
}

#form-container .form-error {
    font-size: 20px;
    color: #ff3b3b;
}
#form-container input.login-input {
    height: 30px;
    background: none;
    margin-bottom: 10px;
    border: none;
    border-bottom: 2px solid #ff669e;
}

#form-container input.login-input:focus {
    outline: none;
}

.login-button {
    display: block;
    margin: 30px auto;
    height: 30px;
    background: #fa80ad;
}
#form-container .login-button:hover {
    background: #ff669e;
}
#form-container .other {
    font-size: 20px;
}
#form-container .other span {
    color: #ff669e;
    cursor: pointer;
}
#board-container {
    margin: 10px;
    text-align: start;
    position: relative;
    /* border: 1px solid red; */
}
.board-list {
    font-size: 18px;
    font-weight: bold;
}

.button-group {
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
    font-size: 14px;
}
.button-group button {
    font-weight: bold;
    font-size: 18px;
    background: inherit;
    width: 130px;
    border: none;
    border-bottom: 2px solid #ff669e;
}
.board-list ul {
    list-style: none;
    margin-left: -15px;
}

.data-list:first-child span:first-child {
    border-bottom: 2px solid #ecc728;
    color: #ecc728;
    font-weight: bold;
}
.data-list:nth-child(2) span:first-child {
    border-bottom: 2px solid #b9bac0;
    color: #b9bac0;
    font-weight: bold;
}
.data-list:nth-child(3) span:first-child {
    border-bottom: 2px solid #f5b26d;
    font-weight: bold;
    color: #f5b26d;
}
.data-list {
    display: flex;
    margin: 5px 0;
}

.data-list span:first-child {
    border-bottom: 1px solid #ff669e;
    width: 30px;
    height: 30px;
    text-align: center;
}

.data-list span:nth-child(2) {
    border-bottom: 1px solid #ff669e;
    width: 200px;
    margin: 0 10px;
    padding-left: 5px;
}

.data-list span:last-child {
    border-bottom: 1px solid #ff669e;
    width: 100px;
    text-align: center;
}

